@charset "utf-8";
/*images news scss*/

.news-images {
  padding: 0 15px 10px 15px;
  border: 1px solid $base_theme_color;
  border-top: 1px solid $base_theme_color;
  overflow: hidden;
  .news-images-header {
    margin-left: -10px;
    height: $base_title_height;
    line-height: $base_title_height;
    font-size: 1.4*$base_font_size;
    font-weight: bold;
    a {
      margin-left: 10px;
      text-decoration: none;
      color: $base_theme_color;
      .fa {
        padding-right: 5px;
      }
    }
  }

  .bd {
    overflow: hidden;
    height: 84px;
    ul li {
      margin-right: 10px;
      float: left;
      img {
        height: 84px;
        width: 150px;
      }
    }
  }
}
